p{
  font-size: 20px;
}

.a{
  text-decoration: none;
}

.page-wrapper{
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-direction: column;
}

.search-header{
  display: flex;
  background-color: #072AC8;
  min-height: 20vh;
  z-index: 0;
}

.search{
  padding: 20px 20px;
  justify-content: space-evenly;
  align-content: center;
  display: flex;
  flex-direction: column;
}

.header-text{
  color: white;
  font-size: 30px;
}

.pokeball-image{
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

.search-field{
  padding: 1px 1px;
  display: flex;
  align-content: center;
  justify-content: flex-start;
  background-color: white;
  min-height: 30px;
  max-height: 30px;
  max-width: 300px;
  border-radius: 20px;
}

.search-field input{
  font-size: 15px;
  width: 210px;
  border: none;
  background-color: white;
  outline: none;
}

.search-field button{
  padding: 0 14px;
  border: none;
  border-radius: 20px;
  background-color: #212529;
  color: white;
  font-size: 15px;
}

.search-field img{
  margin-left: 10px;
  max-height: 30px;
  max-width: 30px;
}

.content{
  margin: 15px 20px 0 20px;
}

.pokemon-grid{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(175px,  1fr));
  grid-template-rows: repeat(auto-fill, minmax(150px, 1fr));
  gap: 30px;
}

.pokemon-card{
  box-sizing: border-box;
  background-color: white;
  padding: 10px 5px;
  border-radius: 15px;
  /*box-shadow:  10px  10px  5px  0 lightgray;*/
  box-shadow:
          10px  0  5px  -5px lightgray,
          -10px 0 5px -5px lightgrey,
          0 10px 5px -5px lightgrey;
  display: flex;
  flex-direction: column;
}

.pokemon-name-id{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.pokemon-name-id span:first-child{
  color: #0023c5;
}

.type-labels{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

}

.pokemon-type-label{
  padding: 1px 15px;
  color: #ffffff;
  font-size: 14px;
  border-radius: 6px;
}

.empty-results{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.empty-results div:first-child{
  font-size: 23px;
  padding: 10px 0;
}

@media screen and (320px <= width < 768px) {
  .pokemon-grid{
    grid-template-columns: repeat(2, minmax(100px, 1fr));
    grid-template-rows: repeat(auto-fill, 1fr);
    gap: 5px;
  }

  .pokemon-name-id span{
    font-size: 1.55vh;
  }

  .pokemon-type-label{
    font-size: 1.5vh;
    padding: 1px 8px;
  }
}

@media screen and (max-width: 350px){
  .search-field{
    max-width: 280px;
  }
}

@media screen and (768px <= width <= 1024px){
  .pokemon-grid{
    grid-template-columns: repeat(5, minmax(100px, 1fr));
    grid-template-rows: repeat(auto-fill, 1fr);
    gap: 5px;
  }

  .pokemon-name-id span{
    font-size: 1.55vh;
  }

  .pokemon-type-label{
    font-size: 1.5vh;
    padding: 1px 8px;
  }
}